<template>
  <div
    class="tab"
  >
    <div style="position:fixed;top:0;left:0;right:0;display:flex;justify-content:space-between;background-color:#fff;padding:10px;z-index:1;border-bottom:1px solid #f3f3f3;">
      <div style="width:16px;display:flex;align-items:center;"><van-icon name="arrow-left" @click="_back" /></div>
      <div>智慧农业</div>
      <div style="width:16px;"></div>
    </div>
    <router-view class="tab-container"></router-view>
    <tabbar />
  </div>
</template>
<script>
import Tabbar from "@/components/beautySalonMallTabber";
export default {
  data: ()=>({

    // 侧滑返回/退出 事件
    sliderAgain: false, // 是否第二次侧滑
    sliderAgainCountdown: 2000, // 再次侧滑退出倒计时，单位毫秒
    sideSliderTimer: null, // 再次侧滑退出倒计时定时器

  }),
  components: {
    Tabbar
  },
  mounted(){
    this.overrideBackbutton();
  },
  destroyed(){
    clearInterval(this.sideSliderTimer);
    if(typeof plus !== 'undefined'){
      plus.key.removeEventListener('backbutton',this.newBackupHandler);
    }
  },
  methods: {
    /**
     * 该方法覆盖手机返回键(以及侧滑退出)的默认逻辑
     * 需要在5+环境下使用
     */
    overrideBackbutton(){
      if(typeof plus !== 'undefined'){
        plus.key.addEventListener('backbutton',this.newBackupHandler)
      }else{
        this.$toast.fail('plus对象不存在！');
      }
    },
    newBackupHandler(){
      // 如果在首页，是 侧滑退出 而不是 侧滑返回
      if(this.$route.name === 'beautySalonMallIndex'){
        if(this.sliderAgain){
          plus.key.removeEventListener('backbutton',this.newBackupHandler);
          plus.runtime.quit();
        }else{
          this.$toast('再次滑动退出程序');
          this.sliderAgain = true;
          
          clearInterval(this.sideSliderTimer);
          this.sideSliderTimer = setInterval(()=>{
            this.sliderAgainCountdown -= 100;
            if(this.sliderAgainCountdown === 0){
              clearInterval(this.sideSliderTimer);
              this.sliderAgain = false;
              this.sliderAgainCountdown = 2000;
            }
          },100)
        }
      }
      // 侧滑返回
      else
      {
        this.$router.back();
      }
    },
    _back(){
      this.$router.back();
    }
  }
};
</script>
<style lang="scss" scoped>
.tab {
  // height: 45px;
  width: 100%;
  box-sizing: border-box;
  height: calc(100% - 50px);
  padding-top: 42px;
}
</style>

